<template>
    <div class="PcbOnline">
      <PcbTop :PcbTopTitle="PcbTitle"></PcbTop>
      <img src="../../../assets/img/Pcb/bg_1.png" alt="" class="title_bg" v-if="pcbOnlien==true">
      <div class="boxdiv CountSwiper">
        <ul class="both pr">
          <li @click="cut(index)" v-for="(data,index) in PcbOnlineHome">{{data}}</li>
        </ul>
      </div>
      <router-view @getMessage="showMsg"></router-view>
    </div>
</template>
<script>
    import Vue from "vue"
    import HomePageBottom from "../../common/HomePageBottom";
    import PcbTop from "../../common/PcbTop";
    import $ from "jquery";
    export default {
        name: "PcbOnline",
        components: {PcbTop, HomePageBottom},
        data() {
            return {
              //像子组件传值
              PcbTitle:{name:"在线计价",class:true,goHome:"Chiefly"},
              PcbOnlineHome:["PCB计价","SMT计价","钢网计价","薄膜面板"],
              //判断是哪个页面
              showMsgIndex:"",
			  pcbOnlien:true,
            }
        },
        methods: {
          cut(index){
            console.log(index);
            switch (index) {
              case 0:
                this.$router.push({path:"PcbHome"});
              this.pcbOnlien=true
                break;
              case 1:
                this.$router.push({path:"SmtHome"});
                this.pcbOnlien=false
                break;
              case 2:
                this.$router.push({path:"SteelOnline"});
                this.pcbOnlien=false
                break;
              default:
                this.$router.push({path:"ThinFilmPanel"});
                this.pcbOnlien=false
                break;
            }
          },
          showMsg (val) {   // methods方法  val即为子组件传过来的值
            this.showMsgIndex=val
          }
        },
      mounted(){
        // 监听并添加蒙版
        let _this=this;
        console.log(_this.$data.showMsgIndex);
        let Onindex=_this.$data.showMsgIndex;
        if(Onindex!=""){
          $($(".boxdiv li")[Onindex]).addClass("choose");
        }else{
          $($(".boxdiv li")[0]).addClass("choose");
        }

        $(document).ready(function() {
          $('.boxdiv').on('scroll', function(e) {
            if ($(this).scrollLeft() === 0) {
              $(this).find("ul").addClass("extrudeLeft");
              $(this).find("ul").removeClass("extrudeRight");
            }
            // console.log($(this).width(),$(this).scrollLeft(),$('.boxdiv ul').width());
            if (($(this).width() + $(this).scrollLeft()) === Math.ceil($('.boxdiv ul').width())) {
              $(this).find("ul").addClass("extrudeRight");
              $(this).find("ul").removeClass("extrudeLeft")
            } else {
              $(this).removeClass("extrudeLeft");
              $(this).removeClass("extrudeRight")
            }
          });
          //滚动动画
          $(".boxdiv li").click(function() {
            $(this).addClass("choose");
            $(this).siblings().removeClass("choose");
            //滚动动画
            if ($(this).index() == 3) {
              // $(this).scrollLeft()="192"
              console.log($(this).scrollLeft())
              $(".boxdiv").animate({
                scrollLeft: 207 //让body的scrollTop等于pos的top，就实现了滚动
              }, 1000);
            } else if ($(this).index() == 2) {
              $(".boxdiv").animate({
                scrollLeft: 0 //让body的scrollTop等于pos的top，就实现了滚动
              }, 1000);
            }
          })
        })
      },
      created() {},
    }
</script>

<style>
  .title_bg{width: 100%}
  .boxdiv {overflow-x: scroll;height: 0.91rem; width: 100%; }
  .boxdiv ul {white-space: nowrap; display: inline-block;}
  .boxdiv ul li {font-size: 0.28rem;padding-left: 0.3rem;margin-right: 0.3rem;padding-right: 0.3rem; height: 0.91rem;line-height: 0.91rem; white-space: nowrap; display: inline-block; margin-right: -4px;position: relative;}
  .boxdiv ul li:last-child {margin-right: 0;}
  .boxdiv ul::-webkit-scrollbar {display: none;}
  .choose:after {content: "";display: table;clear: both;position: absolute;bottom: 0;width: 0.36rem;left: 50%;margin-left: -0.18rem;background: #f90;height: 0.08rem;}
  .CountSwiper .choose {font-size: 0.34rem;font-weight: bold;color: #333;}
  .CountSwiper {width: 100%;height: 0.91rem;position: relative;background: #fff;border-bottom: 1px solid #e3e4e8;}
  body ::-webkit-scrollbar {display: none;}
  .van-collapse-item .van-cell {border-bottom: 1px solid #ebedf0;}
  .van-cell::after {border-bottom: none;}
</style>



