<template>
  <div class="Home">
     <div class="maskings" v-show="DataHome==true"></div>
      <div class="sidebar"  :class="DataHome==true?'transitions':''">
        <p class="sidebartitle">
          <span class="cl_333 f38">捷配专栏</span>
          <span @click="awayWith">x</span>
        </p>
        <ul>
          <li><span>铝基板</span><i class="iconfont">&#xe67c;</i></li>
          <li><span>BOM配单</span><i class="iconfont">&#xe67c;</i></li>
          <li><span>SMT贴片组装</span><i class="iconfont">&#xe67c;</i></li>
          <li><span>3D打印</span><i class="iconfont">&#xe67c;</i></li>
          <li><span>模具注塑</span><i class="iconfont">&#xe67c;</i></li>
          <li><span>薄膜开关面板</span><i class="iconfont">&#xe67c;</i></li>
          <li><span>元器件商城</span><i class="iconfont">&#xe67c;</i></li>
          <li><span>联系我们</span><i class="iconfont">&#xe67c;</i></li>
        </ul>
      </div>
    <div>
      <router-view @acquire="results"  ref='searchVal'></router-view>
      <HomePageBottom :DataName="DataHome" ref='sonV'></HomePageBottom>
    </div>
  </div>
</template>
<script>
import HomePageTop from "./common/HomePageTop";
import HomePageBottom from "./common/HomePageBottom";
export default {
  name: 'Home',
  components: {HomePageBottom,},
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      //侧边栏
      DataHome:"",
    }
  },
  methods:{
    awayWith(){
     this.DataHome=false;
     this.$refs.sonV.sonFun(this.DataHome);
     this.$refs.searchVal.sonFun(this.DataHome);
    },
    // router-view传值方法  子组件中是` this.$emit('acquire',"要传递的数据");
    results (val) {   // methods方法  val即为子组件传过来的值
      this.DataHome=val;
      //向HomePageBottom组件中传递至改变css样式
       this.$refs.sonV.sonFun(this.DataHome);
    }
  },
  created() {
  }
}
</script>
<style scoped>
  .sidebar{width: 4.8rem;height: 100%;position: fixed;background: #333;z-index: 99;transition:all 2s;left: -4.8rem;}
  .sidebar ul{width: 100%;padding: 0 .3rem;}
  .sidebar ul li {width: 100%;height: 1.02rem;line-height: 1.02rem;display: flex;justify-content: space-between;border-bottom: 1px solid #242424;}
  .sidebar ul li span{font-size: .34rem;color: #fff;}
  .sidebar ul li i{color: #fff;font-size: .3rem;}
  .Home .maskings{position: fixed;z-index: 100;width: 100%;height: 100%;left: 4.8rem; }
  .Home .transitions{left:0;}
  .sidebar .sidebartitle{width: 100%;height: .92rem;line-height: .92rem;background: #fff;padding: 0 .3rem;display: flex;justify-content: space-between;}
</style>



