<template>
  <div class="HomePageBottom" :class="sidebarShow==true?'sidebarClass':'sidebarClassNo'">
    <div class="SubjectBottomFather">
      <van-tabbar v-model="active" @change="onChange" >
        <van-tabbar-item  to="/">
          <span>首页</span>
          <template #icon="props">
            <img :src="props.active ? icon1.active : icon1.inactive" />
          </template>
        </van-tabbar-item>
        <van-tabbar-item to="Journalism">
          <span>看点</span>
          <template #icon="props">
            <img :src="props.active ? icon2.active : icon2.inactive" />
          </template>
        </van-tabbar-item>
        <van-tabbar-item  to="PcbHome">
          <span>计价</span>
          <template #icon="props">
            <img :src="props.active ? icon3.active : icon3.inactive" />
          </template>
        </van-tabbar-item>
        <van-tabbar-item  to="ShoppingHome">
          <span>购物车</span>
          <template #icon="props">
            <img :src="props.active ? icon4.active : icon4.inactive" />
          </template>
        </van-tabbar-item>
        <van-tabbar-item  to="Mine">
          <span>我</span>
          <template #icon="props">
            <img :src="props.active ? icon5.active : icon5.inactive" />
          </template>
        </van-tabbar-item>
      </van-tabbar>
    </div>

  </div>
</template>

<script>
	import Vue from "vue"
  export default {
		name: "HomePageBottom",
    props:["DataName"],
		components: {

		},
		data() {
			return {
        active: 0,
        icon1: {active: require('../../assets/img/HomePageBottom/img1_active.png'),inactive: require('../../assets/img/HomePageBottom/img1.png')},
        icon2: {active: require('../../assets/img/HomePageBottom/img2_active.png'),inactive: require('../../assets/img/HomePageBottom/img2.png')},
        icon3: {active: require('../../assets/img/HomePageBottom/img3_active.png'),inactive: require('../../assets/img/HomePageBottom/img3.png')},
        icon4: {active: require('../../assets/img/HomePageBottom/img4_active.png'),inactive: require('../../assets/img/HomePageBottom/img4.png')},
        icon5: {active: require('../../assets/img/HomePageBottom/img5_active.png'),inactive: require('../../assets/img/HomePageBottom/img5.png')},
        //侧边栏的显示隐藏
        sidebarShow:false

			}
		},
		methods: {
      onChange(){
        if(this.active==2){
          this.$store.commit("ThemeIndex",0);
        }else{
          this.$store.commit("ThemeIndex",this.active);
        }

      },
      //关于侧边栏的函数
      sonFun(param){
        this.sidebarShow=param
      }
		},
    mounted() {
    },
    created() {
      console.log(this.$store.state.vuex.ThemeIndexHome);
      if(this.$store.state.vuex.ThemeIndexHome==""){

      }else{
        this.active=this.$store.state.vuex.ThemeIndexHome;
      }

		}
	}
</script>


<style scoped>
 .HomePageBottom .sidebarClass .van-tabbar--fixed{
    transition:all 2s;
    margin-left: 4.8rem;
  }
  .HomePageBottom .sidebarClassNo .van-tabbar--fixed{
    transition:all 2s;
    margin-left: 0;
  }
  .HomePageBottom .van-tabbar--fixed{
    z-index: 10;

  }
  .HomePageBottom .van-tabbar-item__icon img{
      width: .48rem;
    height: .48rem;
  }
  .HomePageBottom .van-tabbar-item--active{
    color: #f90;
  }

  /* 主体底部 */
  .HomePageBottom .SubjectBottomFather{
    height: 1rem;
    /* opacity: 0; */
  }
  .HomePageBottom .SubjectBottom{
    z-index: 1000;
    position: fixed;
    bottom: 0;
    height: 1rem;
    /* line-height: 1rem; */
    width: 100%;
    padding-top: 0.1rem;
    border-top: 1px solid #E3E4E8;
    background: #fff;
  }
  .HomePageBottom .SubjectBottom li{
    float: left;
    width: 20%;
    text-align: center;

  }
  .HomePageBottom .SubjectBottom li img{
    /* width:0.48rem; */
    height: 0.48rem;
    width: 0.48rem;
    display: inline-block;
  }
  .HomePageBottom .SubjectBottom li p{
    color: #666666;
    font-size: 0.22rem;
    /* margin-top: 0.05rem; */
    text-align: center;
  }
  .HomePageBottom .SubjectBottom 	.avtive{
    color: #f90;
  }
</style>



